.block {
	display: flex;
	flex-flow: column nowrap;
	align-items: center;
	width: 100%;
	max-width: 600px;
	margin: 0 auto;
	padding: 8px;
	border: 2px solid navy;
	box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
}

.block-body {
	width: 100%;
	background: none;
}

.block-footer {
	width: 100%;
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-around;
	padding: 8px 0;
}

.block-heading {
	background-color: var(--sc_bg_blockHeading);
}

.block-paragraph {
	background-color: var(--sc_bg_blockParagraph);
}

.block-blockquote {
	background-color: var(--sc_bg_blockBlockquote);
}

.block-orderedList {
	background-color: var(--sc_bg_blockOrderedList);
}

.block-unorderedList {
	background-color: var(--sc_bg_blockUnorderedList);
}

.block-horizontalRule {
	background-color: var(--sc_bg_blockHorizontalRule);
}

.block-code {
	background-color: var(--sc_bg_blockCode);
}
